body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: whitesmoke;
    height: 100%;
}

html,
body,
#root,

.container {
    height: 100%;
    align-content: center;
    justify-content: center;
    display: flex;
  }

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 30px;
    align-items: flex-start;
    margin-left: 55px;
    box-shadow: black;
}

.movies .movie {
    display: flex;
    width: 90%;
    background-color: white;
    font-weight: 300;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 70px;
    padding: 30px;
}

.movie .movie__title {
    font-size: 20px;
}

.movie {
    height: 380px;
    margin-right: 5px;
}
.movie__genres li
.movie .movie__year {
    font-size: 14px;
}

.movie img {
    max-width:  180px;
    margin-top: -50px;
    margin-right: 30px;
}

.movie h3 .movie h5{
    font-weight: 300;
}

.nav {
    height: fit-content;
    position: fixed;
    top: 30px;
    left: 10px;
    background-color: azure;
    padding: 10px;
    font-size: 20px;
    font-style: italic;
}

.movie__summary {
    display: flex;
    width: 500px;
}

.link {
    width: fit-content;
}
